const gallery = document.querySelector('.js-gallery');

galleryItems.forEach(item => {
  const galleryItem = document.createElement('li');
  galleryItem.classList.add('gallery__item');

  const galleryLink = document.createElement('a');
  galleryLink.classList.add('gallery__link');
  galleryLink.setAttribute('href', item.original);

  galleryLink.addEventListener('click', event => {
    event.preventDefault(); // Resim indirme iÅŸlemi durduruldu !!!!
  });

  document.addEventListener('keydown', event => {
    if (event.code === 'Escape') {
    }
  });

  const image = document.createElement('img');
  image.classList.add('gallery__image');
  image.setAttribute('src', item.preview);
  image.setAttribute('data-source', item.original);
  image.setAttribute('alt', item.description);

  galleryItem.appendChild(galleryLink);
  galleryLink.appendChild(image);
  gallery.appendChild(galleryItem);
});

var lightbox = new SimpleLightbox('.gallery li > a', {
  captionsData: 'alt',
  captionDelay: 250,
});
